	    /*width: 100px;*/
	    background-position:center center!important;
	    background: url(data:image/svg+xml;base64,);
	    background-size: 65px 65px !important;
	    background-repeat:no-repeat;
	    background-size: cover;
	    height: 131px;
        margin: 0 auto;
}
.shizi img{
		margin: 0 auto;
}
.shizi .swiper-button-next,.shizi .swiper-button-prev{
		background:transparent;
		opacity: .9;
	    width: 40px;
}
.shizi .swiper-button-next i,.shizi .swiper-button-prev i{
		color: #087d4f;
	    display: block;
	    font-size: 40px;
	    font-weight: bolder;
}
.pic-shadow1{
		background-color:rgba(0,0,0,0.8);
	    position: absolute;
	    top: 0;
	    height: 100%;
	    width: 100%;
	    z-index: 2;
	    display: none;
}
.wrap{
	    position: fixed;
	    left: 50%;
	    top: 20%;
	    z-index: 3;
	    transform: translateX(-50%);
	    -webkit-transform: translateX(-50%);
	    -moz-transform: translateX(-50%);
	    -o-transform: translateX(-50%);
	    -mo-transform: translateX(-50%);
}
@media only screen and (max-width: 980px) {
.change li{
			width: 570px;
}
}
@media only screen and (max-width: 768px) {
.wrap{
			width: 100%;
}
}
@media only screen and (max-width: 680px) {
.change li {
		    width: 520px;
}
}
@media only screen and (max-width: 580px) {
.change li {
		    width: 440px;
}
.change li .imgWrap,.control,.thumbWrap{
			height: 440px;
}
.thumbNext{
			top: 410px;
}
}
@media only screen and (max-width: 480px) {
.wrap{
			top: 15%;
}
.thumbWrap{
			display: none !important;
}
.control{
			width: 100% !important;
			height: 400px;
}
.change li .imgWrap{
			height: 400px;
}
}
@media only screen and (max-width: 414px) {
.change li .imgWrap,.control {
		    height: 350px;
}
.change li {
		    width: 380px;
}
.wrap {
		    top: 25%;
}
}
@media only screen and (max-width: 375px) {
.next,.prev{
		    background-position:-5px -3px;
		    width: 40px;
		    height: 40px;
}
.change li {
		    width: 345px;
}
}
@media only screen and (max-width: 360px) {
.change li {
		    width: 330px;
}
.control ,.change li .imgWrap{
		    height: 350px;
}
.control, .change li .imgWrap {
		    height: 300px;
}
}
@media only screen and (max-width: 320px) {
.wrap {
		    padding: 0;
}
.change li {
		    width: 320px;
}
}

